import { Component, OnInit } from '@angular/core';
import { MatSnackBar, MatDialogRef } from '@angular/material';
import { Validators, FormBuilder, FormArray, FormGroup } from '@angular/forms';
import { ArrayValidators } from '@app/shared/validators/array-validators';
import {
	Project,
	PurchaseOrder,
	Supplier,
	Supply,
	Acknowledgement,
} from '@app/core/models';
import {
	ProjectService,
	SupplierService,
	SupplyService,
	AcknowledgementService,
} from '@app/core';

import { AddOrderComponent } from '@app/shared/components';


@Component({
    templateUrl: './add-purchase-order.component.html',
    styleUrls: ['./add-purchase-order.component.less'],
    providers: [Supplier, PurchaseOrder]
})
export class AddPurchaseOrderComponent extends AddOrderComponent<PurchaseOrder> implements OnInit {
    form: FormGroup = this.fb.group({
        supplier: this.fb.group({
            id: [null],
            name: ['', Validators.required],
            telephone: ['', Validators.required],
            email: [null],
            tax_id: [''],
            addresses: this.fb.array([
                this.fb.group({
                    address1: [''],
                    city: [''],
                    territory: [''],
                    country: [''],
                    zipcode: ['']
                })
            ]),
        }),
        project: this.fb.group({
            id: [null],
            codename: [null],
        }),
        acknowledgement: this.fb.group({
            id: [null],
        }),
        receive_date: [null, Validators.required],
        discount: [''],
        second_discount: [''],
        vat: [7, [Validators.required, Validators.min(1)]],
        remarks: [''],
        items: this.fb.array([
        ], ArrayValidators.minLength(1))
    });
    validation_messages = {
        name: [
            { type: 'required', message: 'Supplier name is required'}
        ],
        delivery_date: [
            { type: 'required', message: 'Delivery date is required' }
        ],
        receive_date: [
            { type: 'required', message: 'Receive date is required' }
        ],
        vat: [
            { type: 'required', message: 'VAT is required' },
            { type: 'min', message: 'A value greater than 0 is required' }
        ],
        items: [
            { type: 'minLength', message: 'At least one item is required' }
        ],
        quantity: [
            { type: 'required', message: 'A quantity is required' },
            { type: 'min', message: 'A value greater than 0 is required' }
        ],
        price: [
            { type: 'required', message: 'A price is required' },
            { type: 'min', message: 'A value greater than 0 is required' }
        ],
        cost: [
            { type: 'required', message: 'A cost is required' },
            { type: 'min', message: 'A value greater than 0 is required' }
        ]
    };

    protected _progress_check = {
        supplier: false,
        project: false,
    };

    constructor(public purchaseOrder: PurchaseOrder,
                public supplier: Supplier,
                public supplierService: SupplierService,
                public supply: Supply,
                public supplyService: SupplyService,
                public project: Project,
                public projectService: ProjectService,
                protected fb: FormBuilder,
                protected snackBar: MatSnackBar,
                protected dialogRef: MatDialogRef<AddPurchaseOrderComponent>,
                public acknowledgement: Acknowledgement,
                public acknowledgementService: AcknowledgementService) {
        
        super(purchaseOrder,
            supplier,
            supplierService,
            supply,
            supplyService,
            project,
            projectService,
            fb,
            snackBar,
            dialogRef);
         
        this.purchaseOrder.items = [];
        this.purchaseOrder.supplier = supplier;
        this.purchaseOrder.supplier.addresses = [{}];

    }


    ngOnInit() {

    }

    public get addresses() {
        return this.form.get('supplier.addresses') as FormArray;
    }

    public getSupplySearchParams(): {} {
        try {
            return {supplier_id: this.getFormControl('supplier.id').value};
        } catch (e) {
            return {};
        }
    }

    public setAcknowledgement(acknowledgement: Acknowledgement | string) {
        const p = typeof(acknowledgement) === 'string' ? {} : acknowledgement;
        this.getFormGroup('acknowledgement').patchValue(p);
    }

    public setSupplier(supplier: Supplier | string) {

        const s = typeof(supplier) === 'string' ? {
            name: supplier,
            email: '',
            telephone: '',
            tax_id: '',
            addresses: [{}]
        } : supplier;
        
        this.getFormGroup('supplier').patchValue(s);

        this.addresses.controls[0].patchValue(s.addresses[0] || {});
    

    }

    protected addCustomItem(description: string): FormGroup {
        const fg = this.fb.group({
            description: [description, Validators.required],
            cost: ['', [Validators.required, , Validators.min(1)]],
            quantity: ['', [Validators.required, , Validators.min(1)]],
            width: [0],
            depth: [0],
            height: [0],
            comments: ['']
        });

        return fg;
    }

    public create(): void {
        if (this.form.valid) {
            this.form.disable();
            this.form.enable();

            const formVal: PurchaseOrder = this.form.value;
            Object.assign(this.purchaseOrder, formVal);

            // Recreate Customer
            const s = new Supplier(this.supplierService);
            Object.assign(s, formVal.supplier);
            this.purchaseOrder.supplier = s;

            this.purchaseOrder.supplier.save(() => {
                this._progress_check.supplier = true;
                this._create();
            });

            // Recreate Project
            if (formVal.project) {
                if (formVal.project.codename) {
                    const p = new Project(this.projectService);
                    Object.assign(p, formVal.project);
                    this.purchaseOrder.project = p;
    
                    if (!this.purchaseOrder.project.id) {
                        this.purchaseOrder.project.save(() => {
                            this._progress_check.project = true;
                            this._create();
                        });
                    } else {
                        this._progress_check.project = true;
                        this._create();
                    }
                } else {
                    delete formVal.project;
                    delete this.purchaseOrder.project;
                    this._progress_check.project = true;
                }
            } else {
                this._progress_check.project = true;
            }
            

        }
    }

    protected _create() {
        if (this._progress_check.project && this._progress_check.supplier) {
            this.form.disable();
            this.snackBar.open('Creating Purchase Order...', 'Dismiss');
            this.purchaseOrder.save(() => {
                this.snackBar.open('Purchase Order Created', 'Dismiss', {duration: 3000});
                this.form.reset();
                this.dialogRef.close(this.purchaseOrder);
            });
        }
    }
}



