import { Component, OnInit, OnDestroy } from '@angular/core';
import { PurchaseOrder } from '@app/core';
import { Observable ,  BehaviorSubject } from 'rxjs';
import {
	PurchaseOrderCardComponent,
	AddPurchaseOrderComponent,
} from '@app/orders/purchase-order/components';
import { DatePipe } from '@angular/common';
import { debounceTime ,  distinctUntilChanged } from 'rxjs/operators';
import { MatDialog } from '@angular/material';


@Component({
  selector: 'app-purchase-orders',
  templateUrl: './purchase-orders.component.html',
  styleUrls: ['./purchase-orders.component.less'],
  providers: [PurchaseOrderCardComponent, DatePipe]
})
export class PurchaseOrdersComponent implements OnInit, OnDestroy {
    private poList: BehaviorSubject<PurchaseOrder[]> = new BehaviorSubject([]);
    public readonly purchaseOrders: Observable<PurchaseOrder[]> = this.poList.asObservable();
    searchTerm: BehaviorSubject<string> = new BehaviorSubject('');

    constructor(purchaseOrder: PurchaseOrder,
                private dialog: MatDialog) {

        purchaseOrder.query().subscribe((x: PurchaseOrder[]) => {
            x = this._sort_list(x);
            this.poList.next(x);
        });

        this.searchTerm.pipe(
            debounceTime(500),
            distinctUntilChanged()
        )
        .subscribe((x: string) => {
            purchaseOrder.query({q: x}).subscribe(
                (results: PurchaseOrder[]) => {
                    const currentList: PurchaseOrder[] = this.poList.value;
                    const keys: number[] = currentList.map((p: PurchaseOrder) => p.id);
                    let newList: PurchaseOrder[] = currentList.concat(results.filter((v: PurchaseOrder) => !keys.includes(v.id)));

                    // Sort
                    newList = this._sort_list(newList);

                    this.poList.next(newList);
                }
            );
        });
    }

    ngOnInit() {
    }

    ngOnDestroy() {
        this.poList.unsubscribe();
        this.searchTerm.unsubscribe();
    }

    updateStage($event: number, status: string): void {

        let purchaseOrder;
        purchaseOrder = this.poList.value.filter((x: PurchaseOrder) => x.id === $event)[0];

        purchaseOrder.status = status;
        purchaseOrder.save((res) => {
            
            const newList = this.poList.value.map((x) => {
                x.status = x.id === res.id ? res.status : x.status;

                return x;
            });

            this.poList.next(newList);
        });

        // Update the current list of purchase orders
        let updatedList = this.poList.value.filter((x: PurchaseOrder) => x.id !== $event);
        updatedList.push(purchaseOrder);

        // Sort
        updatedList = this._sort_list(updatedList);
        
        this.poList.next(updatedList);
    }

    getTotal(status: string) {
        const filteredList: PurchaseOrder[] = this.poList.value.filter((x: PurchaseOrder) => x.status.toLowerCase() === status);
        const totals = filteredList.map((x: PurchaseOrder) => Number(x.total));
        const total = totals.length > 0 ? totals.reduce((sum, y) => sum + y) : 0;
        return total;
    }

    public showAddPurchaseOrder(): void {
        this.dialog.open(AddPurchaseOrderComponent, {
            width: '90vw',
        });
    }

    private _sort_list(list: PurchaseOrder[]): PurchaseOrder[] {
        return list.sort((a, b) => a.id - b.id);
    }
}
